body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
}

a {
  color: #07c;
  font-weight: 700;
  text-decoration: none;
}

a:hover {
  color: #febb02;
  text-decoration: none;
}

.additional-location-info {
  margin-left: 5px;
  -webkit-text-decoration: underline dotted;
          text-decoration: underline dotted;
}

.tool-tip {
  font-size: 12px;
}

.clear-float {
  clear: both
}
.item {
  border: 1px solid rgb(198, 198, 198);
  border-radius: 3px;
  height: 300px;
  padding: 16px;
  width: 812px;
}

.item:hover {
  background-color: #e9f0fa;
  border: 1px solid #c6c6c6;
}

.clicked {
  background-color: #e9f0fa;
  box-shadow: 0 0 5px rgba(81, 203, 238, 1);
  border: 1px solid rgba(81, 203, 238, 1);
}
.hotel-name {
  font-size: 20px;
  font-weight: 700;
}

.location-info {
  text-decoration: underline;
}

.location-info:hover {
  color: #07c;
  text-decoration: underline;
}

.additional-location-info {

}
.guest-favorite {
  float: right;
  font-size: 14px;
  font-weight: bold;
  line-height: 1.9;
}

.reviews {
  padding-right: 0;
}

.review-score-content {
  display: flex;
  float: right;
  margin-top: 4px;
}

.review-score-main {
  display: flex;
  flex-direction: column;
}

.review-score-title {
  color: #003580;
  font-size: 16px;
  font-weight: 500;
  line-height: 1;
}

.review-score-text {
  color: #707070;
  font-size: 12px;
  font-weight: 500;
}

.review-score-badge {
  align-items: center;
  background: #003580;
  border-radius: 6px 6px 6px 0;
  color: #fff;
  display: flex;
  font-size: 16px;
  font-weight: 500;
  height: 32px;
  margin: 0 0 0 5px;
  position: relative;
  text-align: center;
  -webkit-align-items: center;
  -webkit-justify-content: center;
  width: 32px;
}

.review-score-secondary {
  font-size: 14px;
  float: right;
}
.hotel-expectation {
  background: #d1edd4;
  color: #077812;
  font-size: 12px;
}

.hotel-expectation-icon {
  margin-right: 5px;
}

.room-info {
  margin-top: 30px;
}

.room-info-content {
  padding-right: 0;
}

.room-left {
  color: red;
  font-size: 12px;
  font-weight: 800;
}

.room-name {
  font-size: 12px;
  margin-bottom: 3px;
}

.room-name-icons {
  margin-left: 2px;
}

.room-name-icons-user {
  margin-right: 2px;
}
.room-price {
  clear: both;
  color: #48b21b;
  font-size: 18px;
  font-weight: 600;
  float: right;
  margin-bottom: 0;
}

.room-price-content {
  padding-left: 0;
  padding-right: 0;
}

.room-price-for-x-nights{
  background-color: #537bb4;
  border-radius: 4px;
  color: white;
  float: right;
  font-size: 12px;
  margin-bottom: 3px;
  padding: 4px;
  text-align: center;
  width: 60%;
}

.room-price-taxes-and-fees {
  color: #707070;
  font-size: 12px;
  margin-bottom: 0;
  text-align: right;
}

.room-button {
  border: 3px solid #ffffff;
  border-radius: 3px;
  float: right;
  font-size: 14px;
  font-weight: 700;
  margin-top: 8px;
  padding: 8px 16px;
}
.room-button:hover {
  background-color: #003580;
  border: 3px solid #ffffff;
  border-radius: 3px;
  font-size: 14px;
  font-weight: 700;
  padding: 8px 16px;
}

.col-item-content {
  padding-left: 0;
  flex: 0 0 71.666667%;
  max-width: 71.666667%;
}
.col-item-thumbnail {
  flex: 0 0 28.333333%;
  padding-left: 0;
  padding-right: 0;
}
